
@import url("https://use.fontawesome.com/releases/v5.3.1/css/all.css");
@import url("https://fonts.googleapis.com/css?family=Fira+Sans:400,700,800|Lato:300,400");
/*------------------------
mixin that calculates if text needs to be light or dark
depending on the background color passed.

From this W3C document: http://www.webmasterworld.com/r.cgi?f=88&d=9769&url=http://www.w3.org/TR/AERT#color-contrast

Color brightness is determined by the following formula:
((Red value X 299) + (Green value X 587) + (Blue value X 114)) / 1000
------------------------*/
@-webkit-keyframes slideup {
0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    opacity: 0;
}
100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    opacity: 1;
}
}
@keyframes slideup {
0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    opacity: 0;
}
100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    opacity: 1;
}
}
.embedable-footer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  z-index: 1020;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: black;
  color: white;
  font-size: calc(10px + 1.5vh);
}
@media screen and (min-height: 800px) {
.embedable-footer {
      font-size: 1rem;
}
}
.embedable-footer__brand {
    padding-right: 1rem;
    margin-right: 1rem;
    border-right: 2px solid white;
    font-family: "Fira Sans", "Merriweather", Georgia, "Times New Roman", Times, serif;
    font-size: 1.1em;
}
.embedable-footer__lead {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    min-width: 0;
    font-size: 0.9em;
}
.embedable-footer__share-btn {
    border: 1px solid transparent;
    height: 2.5em;
    line-height: 2.5em;
    width: 2.5em;
    font-size: 80%;
    text-align: center;
    border-radius: 50%;
}
.embedable-footer__share-btn.active {
      border-color: white;
}
.embedable-footer__share-btn:hover {
      background: rgba(255, 255, 255, 0.1);
}
.embedable-footer .sharing-options {
    position: absolute;
    bottom: 100%;
    right: 0;
    margin: 0.5rem;
}
.embedable-footer .sharing-options__link {
      opacity: 0;
      -webkit-animation: slideup 200ms forwards;
              animation: slideup 200ms forwards;
}
.embedable-footer .sharing-options__link:nth-child(0) {
        -webkit-animation-delay: 0ms;
                animation-delay: 0ms;
}
.embedable-footer .sharing-options__link:nth-child(1) {
        -webkit-animation-delay: 50ms;
                animation-delay: 50ms;
}
.embedable-footer .sharing-options__link:nth-child(2) {
        -webkit-animation-delay: 100ms;
                animation-delay: 100ms;
}
.embedable-footer .sharing-options__link:nth-child(3) {
        -webkit-animation-delay: 150ms;
                animation-delay: 150ms;
}
.embedable-footer .sharing-options__link:nth-child(4) {
        -webkit-animation-delay: 200ms;
                animation-delay: 200ms;
}
.embedable-footer .sharing-options__link:nth-child(5) {
        -webkit-animation-delay: 250ms;
                animation-delay: 250ms;
}
.embedable-footer .sharing-options__link:nth-child(6) {
        -webkit-animation-delay: 300ms;
                animation-delay: 300ms;
}
.embedable-footer .sharing-options__link:nth-child(7) {
        -webkit-animation-delay: 350ms;
                animation-delay: 350ms;
}
.embedable-footer .sharing-options__link:nth-child(8) {
        -webkit-animation-delay: 400ms;
                animation-delay: 400ms;
}
.embedable-footer .sharing-options__link:nth-child(9) {
        -webkit-animation-delay: 450ms;
                animation-delay: 450ms;
}
.embedable-footer .sharing-options__link:nth-child(10) {
        -webkit-animation-delay: 500ms;
                animation-delay: 500ms;
}



