
@import url("https://use.fontawesome.com/releases/v5.3.1/css/all.css");
@import url("https://fonts.googleapis.com/css?family=Fira+Sans:400,700,800|Lato:300,400");
/*------------------------
mixin that calculates if text needs to be light or dark
depending on the background color passed.

From this W3C document: http://www.webmasterworld.com/r.cgi?f=88&d=9769&url=http://www.w3.org/TR/AERT#color-contrast

Color brightness is determined by the following formula:
((Red value X 299) + (Green value X 587) + (Blue value X 114)) / 1000
------------------------*/
.donate-form {
  font-size: 0.9rem;
}
.donate-form__payment .donate-form__payment__unit {
    width: 120px;
    margin-right: 1rem;
}
@media screen and (max-width: 990px) {
.donate-form__payment .donate-form__payment__unit {
        width: 100%;
        -webkit-box-flex: 0;
            -ms-flex: 0 0 100%;
                flex: 0 0 100%;
}
}
.donate-form__insider__list {
    padding-left: 20px;
}
.donate-form__insider__list-item {
      position: relative;
      margin: 3px 0;
      list-style: none;
}
.donate-form__insider__list-item:before {
        content: "\2713";
        position: absolute;
        left: -16px;
        font-size: 14px;
        font-weight: 700;
        color: #333;
        font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}
@media screen and (max-width: 990px) {
.donate-form__insider__modal__container .col {
      width: 100%;
      -webkit-box-flex: 0;
          -ms-flex: 0 0 100%;
              flex: 0 0 100%;
}
}



