
@import url("https://use.fontawesome.com/releases/v5.3.1/css/all.css");
@import url("https://fonts.googleapis.com/css?family=Fira+Sans:400,700,800|Lato:300,400");
/*------------------------
mixin that calculates if text needs to be light or dark
depending on the background color passed.

From this W3C document: http://www.webmasterworld.com/r.cgi?f=88&d=9769&url=http://www.w3.org/TR/AERT#color-contrast

Color brightness is determined by the following formula:
((Red value X 299) + (Green value X 587) + (Blue value X 114)) / 1000
------------------------*/
.main-header {
  background: rgba(229, 229, 229, 0.8);
  -webkit-box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.1);
  position: relative;
  top: 0;
  width: 100%;
  z-index: 1020;
}
@media (max-width: 991.98px) {
.main-header {
      background: #e5e5e5;
}
}
.main-header.headroom {
    will-change: transform;
    -webkit-transition: -webkit-transform 200ms linear;
    transition: -webkit-transform 200ms linear;
    transition: transform 200ms linear;
    transition: transform 200ms linear, -webkit-transform 200ms linear;
}
.main-header.headroom--unpinned {
      -webkit-transform: translateY(-100%) !important;
              transform: translateY(-100%) !important;
}
.main-header.headroom--pinned {
      -webkit-transform: translateY(0%);
              transform: translateY(0%);
}
.main-header.headroom--frozen {
      -webkit-transform: translateY(0%) !important;
              transform: translateY(0%) !important;
}
.main-header__brand {
    position: relative;
    font-weight: bolder;
    padding-right: 1.5rem;
}
.main-header__brand:after {
      content: "";
      background: #212529;
      width: 2px;
      height: 32px;
      position: absolute;
      right: 0;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
}
@media (max-width: 991.98px) {
.main-header__brand:after {
          display: none;
}
}
.main-header .navbar-toggler {
    background: #e5e5e5;
    position: absolute;
    right: 1rem;
    top: 0.5rem;
    margin: 0;
}
@media (min-width: 992px) {
.main-header .dropdown .nav-link {
      font-size: 1.2rem;
      font-family: "Fira Sans", "Merriweather", Georgia, "Times New Roman", Times, serif;
}
}



